"use client"

import { useState, useEffect, useCallback, useRef } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { 
  Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter 
} from "@/components/ui/dialog"
import { 
  Plus, Play, Square, Slack, LogOut, Activity, 
  Sparkles, UserCheck, Eye, ShieldCheck, Zap, Info, Send, ChevronLeft, ChevronRight, Loader2,
  Trophy
} from "lucide-react"
import logo from "@/lib/images/image.png"

// --- 인터페이스 ---
interface Promotion {
  id: string; 
  name: string;         
  pointAmount: number; 
  totalStock: number;  
  startTime: string;   
  endTime: string;     
  status: "SCHEDULED" | "ACTIVE" | "ENDED";
  etc: string | null;
  createdAt: string; 
}

interface PromotionStatus {
  queueSize: number;
  participantCount: number;
  winnerCount: number;
  totalStock: number;
  remainingStock: number;
}

interface WinnerListResponse {
  userId: string;
  queuePosition: number;
  participatedAt: string;
}

interface AdvisorRequest {
  userId: string;
  userName: string;
  name: string;
  email: string;
  appliedAt: string;
  reason: string;
}

export default function AdminDashboard() {
  const router = useRouter()
  const [isMounted, setIsMounted] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [userRole, setUserRole] = useState<string | null>(null)
  const [userName, setUserName] = useState("")

  const BASE_URL = "http://34.50.7.8:3000"
  const PROMO_API = `${BASE_URL}/v1/promotions`
  const ADMIN_USER_API = `${BASE_URL}/v1/user` 

  const [promotions, setPromotions] = useState<Promotion[]>([])
  const [advisorRequests, setAdvisorRequests] = useState<AdvisorRequest[]>([])
  const [selectedRequest, setSelectedRequest] = useState<AdvisorRequest | null>(null)
  const [isDetailOpen, setIsDetailOpen] = useState(false)
  const [isPromoCreateOpen, setIsPromoCreateOpen] = useState(false)

  // --- 당첨자 & 현황 상태 ---
  const [winners, setWinners] = useState<WinnerListResponse[]>([])
  const [isWinnersOpen, setIsWinnersOpen] = useState(false)
  const [statusData, setStatusData] = useState<PromotionStatus | null>(null)
  const [isStatusOpen, setIsStatusOpen] = useState(false)
  const [targetPromoId, setTargetPromoId] = useState<string | null>(null)
  const [targetPromoName, setTargetPromoName] = useState("")

  const [promoPage, setPromoPage] = useState(0)
  const [totalPromoPages, setTotalPromoPages] = useState(1)
  const itemsPerPage = 5 

  // --- 실시간 갱신을 위한 함수 분리 ---
  const refreshStatus = useCallback(async (id: string) => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${PROMO_API}/${id}/status`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) setStatusData(data.result)
    } catch (e) { console.error("갱신 실패") }
  }, [])

  // 모달이 열려있을 때 3초마다 갱신
  useEffect(() => {
    let timer: NodeJS.Timeout;
    if (isStatusOpen && targetPromoId) {
      timer = setInterval(() => refreshStatus(targetPromoId), 3000)
    }
    return () => clearInterval(timer)
  }, [isStatusOpen, targetPromoId, refreshStatus])

  const fetchWinners = async (id: string, name: string) => {
    const token = localStorage.getItem("accessToken")
    try {
      setTargetPromoName(name)
      const res = await fetch(`${PROMO_API}/${id}/winners`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) { setWinners(data.result); setIsWinnersOpen(true); }
    } catch (error) { alert("조회 실패"); }
  }

  const openStatusMonitor = (id: string, name: string) => {
    setTargetPromoId(id)
    setTargetPromoName(name)
    refreshStatus(id)
    setIsStatusOpen(true)
  }

  const fetchPromotions = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      setIsLoading(true)
      const res = await fetch(`${PROMO_API}?page=${promoPage}&size=${itemsPerPage}`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) {
        setPromotions(data.result.content); 
        setTotalPromoPages(data.result.totalPages);
      }
    } catch (error) { console.error("로드 실패", error); } finally { setIsLoading(false); }
  }, [promoPage])

  const fetchPendingAdvisors = useCallback(async () => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${ADMIN_USER_API}/advisor/pending`, {
        method: "GET",
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      if (data.isSuccess) { setAdvisorRequests(data.result); }
    } catch (error) { console.error("심사 로드 실패", error); }
  }, [])

  useEffect(() => {
    const role = localStorage.getItem("role")
    const name = localStorage.getItem("name") || "관리자"
    if (role !== "MANAGER" && role !== "MASTER") { router.push("/login"); return; }
    setUserRole(role); setUserName(name); setIsMounted(true);
    fetchPromotions(); fetchPendingAdvisors();
  }, [router, fetchPromotions, fetchPendingAdvisors])

  const handleCreatePromotion = async () => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(PROMO_API, {
        method: "POST",
        headers: { "Authorization": `Bearer ${token}`, "Content-Type": "application/json" },
        body: JSON.stringify({ ...newPromo, totalStock: Number(newPromo.totalStock), pointAmount: Number(newPromo.pointAmount), createdAt: new Date().toISOString() })
      })
      const data = await res.json()
      if (data.isSuccess) { alert("생성완료"); setIsPromoCreateOpen(false); fetchPromotions(); }
    } catch (e) { alert("생성실패") }
  }

  const handleTogglePromotion = async (id: string, currentStatus: string) => {
    const token = localStorage.getItem("accessToken")
    const action = currentStatus === "SCHEDULED" ? "start" : "end"
    if (!confirm(`${action === "start" ? "시작" : "종료"}하시겠습니까?`)) return
    try {
      const res = await fetch(`${PROMO_API}/${id}/${action}`, { method: "PATCH", headers: { "Authorization": `Bearer ${token}` } })
      const data = await res.json()
      if (data.isSuccess) fetchPromotions()
    } catch (e) { alert("변경실패") }
  }

  const handleProcessAdvisor = async (userId: string, isApprove: boolean) => {
    const token = localStorage.getItem("accessToken")
    try {
      const res = await fetch(`${ADMIN_USER_API}/advisor/${userId}/approve`, { method: "POST", headers: { "Authorization": `Bearer ${token}` } })
      const data = await res.json()
      if (data.isSuccess) { alert("처리완료"); setIsDetailOpen(false); fetchPendingAdvisors(); }
    } catch (e) { alert("심사실패") }
  }

  const handleSlackTest = async () => {
    const token = localStorage.getItem("accessToken");
    try {
      const res = await fetch(`${PROMO_API}/test/monitoring/test-slack-only`, { method: "POST", headers: { "Authorization": `Bearer ${token}` } });
      if ((await res.json()).isSuccess) alert("테스트 성공");
    } catch (e) { alert("오류") }
  };

  const [newPromo, setNewPromo] = useState({ name: "", pointAmount: "", totalStock: "", startTime: "", endTime: "" })

  if (!isMounted) return null

  return (
    <div className="min-h-screen bg-background text-foreground font-sans">
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/admin" className="flex items-center gap-2">
            <Image src={logo} alt="Next Me" width={100} height={28} className="h-6 sm:h-7 w-auto" priority />
            <Badge variant="outline" className="text-[9px] border-primary text-primary font-bold uppercase">{userRole} ADMIN</Badge>
          </Link>
          <div className="flex items-center gap-3">
            <Avatar className="h-8 w-8 border-2 border-primary/20"><AvatarFallback className="bg-primary text-primary-foreground text-[10px] font-black">{userName?.charAt(0)}</AvatarFallback></Avatar>
            <Button variant="ghost" size="icon" onClick={() => {localStorage.clear(); router.push("/login");}}><LogOut className="h-4 w-4" /></Button>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-6 sm:py-8">
        <div className="flex flex-col md:flex-row justify-between items-start md:items-end gap-4 mb-8">
          <h1 className="text-2xl font-black tracking-tighter uppercase italic tracking-tight">Welcome, {userName}.</h1>
          <div className="flex gap-2 w-full md:w-auto">
            <Button variant="outline" size="sm" className="flex-1 md:flex-none rounded-xl h-9 text-xs" onClick={handleSlackTest}><Slack className="h-3.5 w-3.5 mr-2" /> Test</Button>
            <Button size="sm" className="flex-1 md:flex-none bg-primary rounded-xl h-9 text-xs" onClick={() => alert("준비 중")}><Send className="h-3.5 w-3.5 mr-2" /> Report</Button>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          <div className="lg:col-span-2">
            <Card className="p-4 sm:p-6 border-border shadow-none rounded-2xl bg-card/50 overflow-hidden">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-lg font-black italic uppercase flex items-center gap-2"><Sparkles className="h-4 w-4 text-primary" /> Promotions</h2>
                <Button size="sm" onClick={() => setIsPromoCreateOpen(true)} className="rounded-xl h-8 text-xs font-bold px-4"><Plus className="h-3 w-3 mr-1" /> New</Button>
              </div>

              {/* 한눈에 보기 최적화 테이블 */}
              <div className="overflow-x-auto">
                <table className="w-full text-left font-sans">
                  <thead className="text-muted-foreground text-[9px] font-black uppercase border-b border-border/40">
                    <tr>
                      <th className="py-3 w-[30%]">Campaign</th>
                      <th className="px-2 py-3 w-[35%]">Schedule</th>
                      <th className="py-3 text-center w-[15%]">Status</th>
                      <th className="py-3 text-right w-[20%]">Action</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y divide-border/30">
                    {promotions.map((p) => (
                      <tr key={p.id} className="group hover:bg-secondary/5">
                        <td className="py-4 pr-2">
                          <p className="font-bold text-xs uppercase italic truncate max-w-[120px] sm:max-w-none">{p.name}</p>
                          <p className="text-[9px] text-primary font-mono font-bold mt-1">{p.pointAmount.toLocaleString()}P · {p.totalStock}EA</p>
                        </td>
                        <td className="px-2 py-4 font-mono text-[9px] leading-tight whitespace-nowrap">
                          <div className="flex items-center gap-1.5 opacity-70"><div className="w-1 h-1 bg-green-500 rounded-full" /> {p.startTime.slice(5, 16).replace('T', ' ')}</div>
                          <div className="flex items-center gap-1.5 opacity-70"><div className="w-1 h-1 bg-destructive rounded-full" /> {p.endTime.slice(5, 16).replace('T', ' ')}</div>
                        </td>
                        <td className="py-4 text-center">
                          <Badge variant={p.status === "ACTIVE" ? "default" : "secondary"} className="rounded px-1.5 py-0 text-[8px] font-black h-4 uppercase">{p.status}</Badge>
                        </td>
                        <td className="py-4 text-right">
                          <div className="flex justify-end gap-0.5">
                            {p.status === "SCHEDULED" && <Button size="icon" variant="ghost" className="h-7 w-7 text-green-600" onClick={() => handleTogglePromotion(p.id, "SCHEDULED")}><Play className="h-3.5 w-3.5 fill-current" /></Button>}
                            {p.status === "ACTIVE" && <Button size="icon" variant="ghost" className="h-7 w-7 text-destructive" onClick={() => handleTogglePromotion(p.id, "ACTIVE")}><Square className="h-3.5 w-3.5 fill-current" /></Button>}
                            <Button size="icon" variant="ghost" className="h-7 w-7 text-orange-500" onClick={() => fetchWinners(p.id, p.name)}><Trophy className="h-3.5 w-3.5" /></Button>
                            <Button size="icon" variant="ghost" className="h-7 w-7 text-primary" onClick={() => openStatusMonitor(p.id, p.name)}><Info className="h-3.5 w-3.5" /></Button>
                          </div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </Card>
          </div>

          <div className="space-y-6">
            <Card className="p-6 border-border shadow-none bg-secondary/10 rounded-2xl">
              <h2 className="text-sm font-black italic uppercase mb-4 flex items-center gap-2"><UserCheck className="h-4 w-4" /> Pending Advisors</h2>
              <div className="space-y-3">
                {advisorRequests.map(req => (
                  <div key={req.userId} className="flex items-center justify-between p-3 bg-background rounded-xl border border-border/50">
                    <div className="flex items-center gap-3 overflow-hidden">
                      <Avatar className="h-8 w-8"><AvatarFallback className="text-[10px]">{req.name?.[0]}</AvatarFallback></Avatar>
                      <div className="truncate"><p className="font-bold text-[11px] truncate">{req.name}</p><p className="text-[9px] text-muted-foreground font-mono">{req.appliedAt?.split('T')[0]}</p></div>
                    </div>
                    <Button variant="outline" size="sm" className="h-7 text-[10px] rounded-lg px-3 shrink-0" onClick={() => { setSelectedRequest(req); setIsDetailOpen(true); }}>Review</Button>
                  </div>
                ))}
              </div>
            </Card>
          </div>
        </div>
      </main>

      {/* --- 당첨자 목록 모달 (순번 자동화 적용) --- */}
      <Dialog open={isWinnersOpen} onOpenChange={setIsWinnersOpen}>
        <DialogContent className="w-[92vw] sm:max-w-[400px] rounded-[1.5rem] p-6 border-none">
          <DialogHeader><DialogTitle className="font-black italic uppercase text-lg flex items-center gap-2 text-orange-500"><Trophy className="h-5 w-5" /> Winners: {targetPromoName}</DialogTitle></DialogHeader>
          <div className="max-h-[350px] overflow-y-auto space-y-2 my-4 pr-1">
            {winners.map((w, index) => (
              <div key={w.userId + index} className="flex items-center justify-between p-3 rounded-xl bg-secondary/10 border border-border/40">
                <div className="flex items-center gap-3">
                  <span className="text-xs font-black italic text-orange-600 w-5">#{index + 1}</span>
                  <div>
                    <p className="text-[11px] font-mono font-bold truncate w-[180px]">{w.userId}</p>
                    <p className="text-[9px] font-mono text-muted-foreground opacity-70">{w.participatedAt.replace('T', ' ').slice(5, 19)}</p>
                  </div>
                </div>
                <Badge className="text-[8px] font-black uppercase h-5 bg-orange-100 text-orange-600 border-none">Win</Badge>
              </div>
            ))}
          </div>
          <Button onClick={() => setIsWinnersOpen(false)} className="w-full h-11 rounded-xl font-black uppercase italic tracking-widest">Close</Button>
        </DialogContent>
      </Dialog>

      {/* --- 실시간 현황 모달 (실시간 갱신 로직 추가) --- */}
      <Dialog open={isStatusOpen} onOpenChange={setIsStatusOpen}>
        <DialogContent className="w-[92vw] sm:max-w-[420px] rounded-[2rem] p-6 sm:p-8 border-none">
          <DialogHeader>
            <DialogTitle className="font-black italic text-lg uppercase flex items-center justify-between">
              <span className="flex items-center gap-2"><Activity className="h-5 w-5 text-primary animate-pulse" /> Live Monitor</span>
              <Badge variant="outline" className="text-[8px] font-mono animate-pulse border-primary text-primary">Live</Badge>
            </DialogTitle>
          </DialogHeader>
          {statusData ? (
            <div className="grid grid-cols-2 gap-3 py-4">
              <div className="p-3 bg-secondary/10 rounded-xl border border-border/50"><p className="text-[8px] font-black uppercase text-muted-foreground mb-1">Queue</p><p className="text-lg font-black">{statusData.queueSize.toLocaleString()}</p></div>
              <div className="p-3 bg-secondary/10 rounded-xl border border-border/50"><p className="text-[8px] font-black uppercase text-muted-foreground mb-1">Users</p><p className="text-lg font-black">{statusData.participantCount.toLocaleString()}</p></div>
              <div className="p-3 bg-primary/10 rounded-xl border border-primary/20 text-primary"><p className="text-[8px] font-black uppercase mb-1">Winners</p><p className="text-lg font-black">{statusData.winnerCount.toLocaleString()}</p></div>
              <div className="p-3 bg-secondary/10 rounded-xl border border-border/50"><p className="text-[8px] font-black uppercase text-muted-foreground mb-1">Total</p><p className="text-lg font-black">{statusData.totalStock.toLocaleString()}</p></div>
              <div className="col-span-2 p-5 bg-orange-500/10 rounded-xl border border-orange-500/20 text-center">
                <p className="text-[9px] font-black uppercase text-orange-600 mb-1">Remaining Stock</p>
                <p className="text-4xl font-black text-orange-600 tracking-tighter">{statusData.remainingStock.toLocaleString()}</p>
              </div>
            </div>
          ) : <div className="py-10 flex justify-center"><Loader2 className="animate-spin text-primary" /></div>}
          <Button className="w-full h-12 font-black rounded-xl uppercase italic tracking-widest" onClick={() => setIsStatusOpen(false)}>Exit Monitor</Button>
        </DialogContent>
      </Dialog>

      {/* --- 전문가 심사 모달 --- */}
      <Dialog open={isDetailOpen} onOpenChange={setIsDetailOpen}>
        <DialogContent className="w-[92vw] sm:max-w-[360px] rounded-[1.5rem] p-6 border-none">
          <DialogHeader><DialogTitle className="font-black italic uppercase text-lg">Advisor Review</DialogTitle></DialogHeader>
          {selectedRequest && (
            <div className="space-y-4">
              <div className="p-4 bg-secondary/10 rounded-xl text-[11px] font-bold italic">"{selectedRequest.reason}"</div>
              <div className="grid grid-cols-2 gap-2">
                <Button variant="outline" className="h-11 rounded-xl font-black uppercase italic text-[10px]" onClick={() => handleProcessAdvisor(selectedRequest.userId, false)}>Reject</Button>
                <Button className="h-11 rounded-xl font-black uppercase italic text-[10px]" onClick={() => handleProcessAdvisor(selectedRequest.userId, true)}>Approve</Button>
              </div>
            </div>
          )}
        </DialogContent>
      </Dialog>

      {/* --- 캠페인 생성 모달 --- */}
      <Dialog open={isPromoCreateOpen} onOpenChange={setIsPromoCreateOpen}>
        <DialogContent className="w-[92vw] sm:max-w-[400px] rounded-[2rem] p-6 border-none shadow-2xl">
          <DialogHeader><DialogTitle className="font-black italic uppercase text-lg">New Promotion</DialogTitle></DialogHeader>
          <div className="grid gap-3 py-2">
            <div className="space-y-1"><Label className="text-[9px] font-black uppercase">Name</Label><Input value={newPromo.name} onChange={(e) => setNewPromo({...newPromo, name: e.target.value})} className="bg-secondary/20 border-none h-10 rounded-xl" /></div>
            <div className="grid grid-cols-2 gap-2">
              <div className="space-y-1"><Label className="text-[9px] font-black uppercase">Points</Label><Input type="number" value={newPromo.pointAmount} onChange={(e) => setNewPromo({...newPromo, pointAmount: e.target.value})} className="bg-secondary/20 border-none h-10 rounded-xl" /></div>
              <div className="space-y-1"><Label className="text-[9px] font-black uppercase">Stock</Label><Input type="number" value={newPromo.totalStock} onChange={(e) => setNewPromo({...newPromo, totalStock: e.target.value})} className="bg-secondary/20 border-none h-10 rounded-xl" /></div>
            </div>
            <div className="grid grid-cols-2 gap-2">
              <div className="space-y-1"><Label className="text-[9px] font-black uppercase">Start</Label><Input type="datetime-local" value={newPromo.startTime.slice(0,16)} onChange={(e) => setNewPromo({...newPromo, startTime: e.target.value + ":00"})} className="bg-secondary/20 border-none h-10 rounded-xl text-[10px]" /></div>
              <div className="space-y-1"><Label className="text-[9px] font-black uppercase">End</Label><Input type="datetime-local" value={newPromo.endTime.slice(0,16)} onChange={(e) => setNewPromo({...newPromo, endTime: e.target.value + ":00"})} className="bg-secondary/20 border-none h-10 rounded-xl text-[10px]" /></div>
            </div>
          </div>
          <Button onClick={handleCreatePromotion} className="w-full h-12 font-black rounded-xl uppercase italic mt-4">Create</Button>
        </DialogContent>
      </Dialog>
    </div>
  )
}